#maze_account_page {
  // background-image: url("../../../../assets/images/bg1.png"),
  //   url("../../../../assets/images/bg1.png");
  // background-color: #fff;
  // background-repeat: repeat-x, repeat-x;
  // background-position: center bottom, 0% -10%;
  // background-size: 600px, 400px;
  height: calc(100% - 64px);

  #index-banner {
    height: calc(100% - 64px);
  }

  .card-tile-container {
    display: table;
    .card-tile {
      display: table-cell;
      .icon-block {
        padding: 10px 20px 20px 20px;
        background-color: #ffffffd4;
        border-radius: 18px;
      }
    }
  }

  .img-wrapper {
    display: none;
    img {
      height: 300px;
      width: auto;
    }
  }
}

#maze_account_page::before {
  background-image: url("../../../../assets/images/body-clip.svg");
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
}
